@keyframes ToggleShow {
	from {
		height: 0;
		padding: 0;
		opacity: 0;
		transform: scaleY(0);
	}
	to {
		height: auto;
		padding: 20px;
		opacity: 1;
		transform: scaleY(1);
	}
}

@keyframes ToggleHide {
	from {
		height: auto;
		padding: 20px;
		opacity: 1;
		transform: scaleY(1);
	}
	to {
		height: 0;
		padding: 0;
		opacity: 0;
		transform: scaleY(0);
	}
}
